import React from "react";
import styled from "styled-components";

// Variables
import { prefix, color } from "../../shared/styles.js";
import { icons } from "../../shared/icons.js";

// Styles
const StyledTop = styled((props) => <div {...props} />)`
  background-color: ${color.botti};
  height: 40px;

  .container {
    align-items: center;
    display: flex;
    justify-content: flex-start;
    margin: 0 auto;
    max-width: 1080px;
    width: 80%;

    a {
      text-decoration: none;
      display: flex;
      align-items: center;
      margin-right: 10px;

      &:last-child {
        margin-right: 0;
      }

      span {
        color: ${color.white};
        font-size: 15px;
        font-weight: 600;
      }

      svg {
        width: 15px;
        margin-right: 10px;

        path {
          fill: ${color.white};
        }
      }
    }
  }
`;

const Top = ({ phone, email }) => (
  <StyledTop className={`${prefix}-header-top`}>
    <div className="container">
      <a href={`tel:$${phone.href}`}>
        <span dangerouslySetInnerHTML={{ __html: icons[phone.icon] }} />
        <span>{phone.label}</span>
      </a>

      <a href={`mailto:${email.href}`}>
        <span dangerouslySetInnerHTML={{ __html: icons[email.icon] }} />
        <span>{email.label}</span>
      </a>
    </div>
  </StyledTop>
);

export default Top;
